
.link {
  color: #61dafb;
}

.header-color {
  background: #373B44;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #4286f4, #373B44);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #4286f4, #373B44); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.body-container{
  text-align: center;
  font-family: 'MaisonNeue','Helvetica',sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 1.625;
  color: whitesmoke;
  height: 100%;
}

.body-avatar > img {
  border-radius: 50%;
  margin: auto;
}

.body-avatar {
  padding-top: 100px; 
  width: 100%;
}

.body-content {
  margin: 0 auto 0 auto;
  width: 80%;
}

.body-social{
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  width: 10%;
  margin: auto;  
}

.body-social a > i {
  font-size: 3.5em;
  padding: 8px;
}

.social-github {
  color: #373B44;
}

.social-linkedin {
  color: #4dabf7;
}

.menu-title {
  background: #0f2027;
  color: white;
}

.menu-link {
  padding: 0; 
  margin: 0;
}

.menu-link-content {
  color: white;
}

.menu-link-content > a:hover{
  color: #0f2027;
}